.menu {
  display: flex;
  flex-direction: column;

  background: $light-blue;
  height: 100vh;

  transition: all 0.3s;
  transform: translateX(-100%);

  padding: 0 16px;

  &:target {
    transform: translateX(0);
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 64px;
    width: 100%;
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 24px;
    height: 100%;
    list-style: none;

    color: $black;
    font-size: 16px;
    font-weight: 400;
    line-height: 150%; /* 24px */
    letter-spacing: -0.16px;
  }
}
